import React, { useState, useEffect, useRef } from "react";
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import { getUsers, addUser } from "../../actions/userAction";
import { useDispatch, useSelector } from "react-redux";
import Loading from "../layout/Loading";

import FirstCurve from "../../Pic/formSvg/curve1.svg";
import SecondCurve from "../../Pic/formSvg/curve2.svg";

const UserAccount = () => {
  const getUser = useSelector((state) => state.users);
  const { users, loading } = getUser;
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getUsers());
  }, []);

  const [bool, setBool] = useState(false);

  // initial formik value
  const initialValue = {
    email: "",
    password: "",
  };

  const login = useRef();
  const logup = useRef();

  if (loading || users === null) {
    return (
      <div
        style={{
          height: "90vh",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Loading />
      </div>
    );
  }

  // handle form slide
  const goLogup = () => {
    if (bool === false) {
      setBool(true);
      login.current.style.transform = "translateX(-335px) rotateY(90deg)";
      logup.current.style.transform = "translateX(0px) rotateY(0deg)";
    }
  };

  const goLogin = () => {
    if (bool === true) {
      setBool(false);
      login.current.style.transform = "translateX(0px) rotateY(0deg)";
      logup.current.style.transform = "translateX(335px) rotateY(90deg)";
    }
  };

  // get users email
  const emailAddresses = users.map((user) => user.email);

  const handleSubmit = (values, { setSubmitting, resetForm }) => {
    setTimeout(() => {
      alert("   " + JSON.stringify(values.email, null, 2));
      resetForm();
      setSubmitting(false);
      dispatch(addUser(values));
    }, 1000);
  };

  const lowercaseRegex = /(?=.*[a-z])/;
  const uppercaseRegex = /(?=.*[A-Z])/;
  const numericRegex = /(?=.*[0-9])/;

  // signupSchema
  const signupSchema = Yup.object().shape({
    email: Yup.string()
      .email("   ")
      .notOneOf(emailAddresses, "  ")
      .required(""),
    password: Yup.string()
      .matches(uppercaseRegex, "    ")
      .matches(lowercaseRegex, "    ")
      .matches(numericRegex, "   ")
      .min(8, " 8   ")
      .required(""),
  });

  return (
    <div className="accountWrapper">
      <div className="logup" ref={logup}>
        <h3>    </h3>
        <img src={FirstCurve} className="firstCurve" alt="curve" />
        <img src={SecondCurve} className="secondCurve" alt="curve" />
        <Formik
          initialValues={initialValue}
          onSubmit={handleSubmit}
          validationSchema={signupSchema}
        >
          {({ errors, touched }) => {
            return (
              <Form>
                <Field type="email" name="email" placeholder="" required autoComplete="off" />
                {touched.email && errors.email ? (
                  <p className="msg">{errors.email}</p>
                ) : null}

                <Field
                  type="password"
                  name="password"
                  placeholder=" "
                  required
                  autoComplete="off"
                />
                {touched.password && errors.password ? (
                  <p className="msg">{errors.password}</p>
                ) : null}

                <button type="submit"> </button>
              </Form>
            );
          }}
        </Formik>
        <p>
          {" "}
          <span className="goLogin" onClick={goLogin}>
            {" "}
          </span>
           
        </p>
      </div>

      <div className="login" ref={login}>
        <h3>   </h3>
        <img src={FirstCurve} className="firstCurve" alt="curve" />
        <img src={SecondCurve} className="secondCurve" alt="curve" />
        <form action="#!">
          <input type="email" name="email" placeholder="" required autoComplete="off" />
          <input
            type="password"
            name="password"
            placeholder=" "
            required
            autoComplete="off"
          />
          <button type="submit"></button>
        </form>
        <p>
            {" "}
          <span className="goLogup" onClick={goLogup}>
             {" "}
          </span>
          
        </p>
      </div>
    </div>
  );
};

export default UserAccount;
